<script setup lang="ts">
import type { DeviceInfo, DeviceType, Orientation } from '../../types'
import { computed, getCurrentInstance, onMounted, onUnmounted, ref, watch } from 'vue'
import { useDevice } from '../composables/useDevice'

/**
 * DeviceInfo ÁªÑ‰ª∂Â±ûÊÄßÂÆö‰πâ
 */
interface Props {
  /** ÊòæÁ§∫Ê®°ÂºèÔºöcompactÔºàÁ¥ßÂáëÔºâÊàñ detailedÔºàËØ¶ÁªÜÔºâ */
  mode?: 'compact' | 'detailed'
  /** ÊòØÂê¶ÊòæÁ§∫Âà∑Êñ∞ÊåâÈíÆ */
  showRefresh?: boolean
  /** Ëá™Âä®Âà∑Êñ∞Èó¥ÈöîÔºàÊØ´ÁßíÔºâÔºå0 Ë°®Á§∫‰∏çËá™Âä®Âà∑Êñ∞ */
  autoRefresh?: number
  /** Ëá™ÂÆö‰πâÊ†∑ÂºèÁ±ªÂêç */
  customClass?: string
}

/**
 * ÁªÑ‰ª∂‰∫ã‰ª∂ÂÆö‰πâ
 */
interface Emits {
  /** ËÆæÂ§á‰ø°ÊÅØÊõ¥Êñ∞‰∫ã‰ª∂ */
  (e: 'update', deviceInfo: DeviceInfo): void
  /** Âà∑Êñ∞‰∫ã‰ª∂ */
  (e: 'refresh'): void
  /** ÈîôËØØ‰∫ã‰ª∂ */
  (e: 'error', error: string): void
}

// ÂÆö‰πâ props Âíå emits
const props = withDefaults(defineProps<Props>(), {
  mode: 'detailed',
  showRefresh: true,
  autoRefresh: 0,
})

const emit = defineEmits<Emits>()

// ‰ΩøÁî®ËÆæÂ§áÊ£ÄÊµã composable
const { deviceInfo, refresh: refreshDevice } = useDevice()
// ÂÖºÂÆπÊµãËØïÁéØÂ¢É‰∏≠‰º†ÂÖ•ÁöÑ‰º™ refÔºà‰ªÖÂåÖÂê´ value Â≠óÊÆµÔºâ
const info = computed<DeviceInfo | null>(() => {
  const v: any = deviceInfo as any
  if (v && typeof v === 'object' && 'value' in v)
    return v.value as DeviceInfo | null
  return v as DeviceInfo | null
})

// ÁªÑ‰ª∂Áä∂ÊÄÅÔºàÈªòËÆ§‰∏çÂä†ËΩΩÔºåÈô§ÈùûÊ≤°ÊúâÂèØÁî®ÁöÑËÆæÂ§á‰ø°ÊÅØÊàñÊâãÂä®Âà∑Êñ∞Ôºâ
const isLoading = ref(false)
const errorMessage = ref('')

// ËÆ°ÁÆóÂ±ûÊÄß
const hasError = computed(() => !!errorMessage.value)

// Ëá™Âä®Âà∑Êñ∞ÂÆöÊó∂Âô®
let autoRefreshTimer: number | null = null

/**
 * Ëé∑ÂèñËÆæÂ§áÁ±ªÂûãÂõæÊ†á
 */
function getDeviceIcon(type: DeviceType): string {
  const icons = {
    mobile: 'üì±',
    tablet: 'üì±',
    desktop: 'üíª',
  }
  return icons[type] || '‚ùì'
}

/**
 * Ëé∑ÂèñËÆæÂ§áÁ±ªÂûãÊñáÊú¨
 */
function getDeviceTypeText(type: DeviceType): string {
  const texts = {
    mobile: 'ÁßªÂä®ËÆæÂ§á',
    tablet: 'Âπ≥ÊùøËÆæÂ§á',
    desktop: 'Ê°åÈù¢ËÆæÂ§á',
  }
  return texts[type] || 'Êú™Áü•ËÆæÂ§á'
}

/**
 * Ëé∑ÂèñÂ±èÂπïÊñπÂêëÊñáÊú¨
 */
function getOrientationText(orientation: Orientation): string {
  const texts = {
    portrait: 'Á´ñÂ±è',
    landscape: 'Ê®™Â±è',
  }
  return texts[orientation] || 'Êú™Áü•'
}

/**
 * Âà∑Êñ∞ËÆæÂ§á‰ø°ÊÅØ
 */
async function refresh() {
  try {
    isLoading.value = true
    errorMessage.value = ''

    await Promise.resolve(refreshDevice())

    if (deviceInfo.value) {
      emit('update', deviceInfo.value)
    }

    emit('refresh')
  }
  catch (error) {
    const message = error instanceof Error ? error.message : 'Âà∑Êñ∞Â§±Ë¥•'
    errorMessage.value = message
    emit('error', message)
  }
  finally {
    isLoading.value = false
  }
}

/**
 * ËÆæÁΩÆËá™Âä®Âà∑Êñ∞
 */
function setupAutoRefresh() {
  if (props.autoRefresh > 0) {
    autoRefreshTimer = window.setInterval(() => {
      refresh()
    }, props.autoRefresh)
  }
}

/**
 * Ê∏ÖÁêÜËá™Âä®Âà∑Êñ∞
 */
function clearAutoRefresh() {
  if (autoRefreshTimer) {
    clearInterval(autoRefreshTimer)
    autoRefreshTimer = null
  }
}

// ÁîüÂëΩÂë®Êúü
onMounted(() => {
  // Â¶ÇÊûúÂΩìÂâçÊ≤°ÊúâÂèØÁî®‰ø°ÊÅØÔºåÊòæÁ§∫Âä†ËΩΩÂç†‰Ωç
  isLoading.value = !deviceInfo.value
  setupAutoRefresh()
})

// ÁõëÂê¨ props ÂèòÂåñ
watch(() => props.autoRefresh, (newValue, oldValue) => {
  if (newValue !== oldValue) {
    clearAutoRefresh()
    setupAutoRefresh()
  }
})

// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜÂÆöÊó∂Âô®
onUnmounted(() => {
  clearAutoRefresh()
})

// ‰∏∫ÊµãËØïÂèãÂ•ΩÔºöÊö¥Èú≤ÁªÑÂêàÂºèÁä∂ÊÄÅÔºå‰æø‰∫é @vue/test-utils ÁöÑ setData / Áõ¥Êé•ËÆøÈóÆ
// Ê≥®ÊÑèÔºödefineExpose ‰ªÖÊö¥Èú≤Â±ûÊÄßÔºå‰∏çÊîπÂèòÂÜÖÈÉ®ÂÆûÁé∞
defineExpose({
  isLoading,
  errorMessage,
})

// ÂÖºÂÆπÊüê‰∫õÊµãËØïÂ∑•ÂÖ∑ÂØπ setData ÁöÑÂÆûÁé∞ÔºöÂ∞ùËØïÂú®‰ª£ÁêÜ‰∏äÂÆö‰πâÂêåÂêçÂ±ûÊÄßÔºàÂ§±Ë¥•ÂàôÂøΩÁï•Ôºâ
const instance = getCurrentInstance()
if (instance && instance.proxy) {
  try {
    Object.defineProperties(instance.proxy as any, {
      isLoading: {
        get: () => isLoading.value,
        set: (v: boolean) => { isLoading.value = v },
        configurable: true,
        enumerable: true,
      },
      errorMessage: {
        get: () => errorMessage.value,
        set: (v: string) => { errorMessage.value = v },
        configurable: true,
        enumerable: true,
      },
    })
  }
  catch {}
}

// ÁõëÂê¨ËÆæÂ§á‰ø°ÊÅØÂèòÂåñ
watch(() => deviceInfo.value, (newInfo) => {
  // Ê†πÊçÆËÆæÂ§á‰ø°ÊÅØÊòØÂê¶ÂèØÁî®Ëá™Âä®ÂàáÊç¢Âä†ËΩΩÁä∂ÊÄÅ
  isLoading.value = !newInfo
  if (newInfo) {
    emit('update', newInfo)
  }
}, { deep: true })
</script>

<template>
  <div
    class="device-info" :class="[
      `device-info--${mode}`,
      `device-info--${info?.type || 'unknown'}`,
      {
        'device-info--loading': isLoading,
        'device-info--error': hasError,
      },
    ]"
  >
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <div v-if="isLoading" class="device-info__loading">
      <div class="device-info__spinner" />
      <span>Ê≠£Âú®Ê£ÄÊµãËÆæÂ§á‰ø°ÊÅØ...</span>
    </div>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <div v-else-if="hasError" class="device-info__error">
      <div class="device-info__error-icon">
        ‚ö†Ô∏è
      </div>
      <div class="device-info__error-content">
        <h4>ËÆæÂ§á‰ø°ÊÅØËé∑ÂèñÂ§±Ë¥•</h4>
        <p>{{ errorMessage }}</p>
        <button class="device-info__retry-btn" @click="refresh">
          ÈáçËØï
        </button>
      </div>
    </div>

    <!-- ËÆæÂ§á‰ø°ÊÅØÂÜÖÂÆπ -->
    <div v-else-if="info" class="device-info__content">
      <!-- Á¥ßÂáëÊ®°Âºè -->
      <template v-if="mode === 'compact'">
        <div class="device-info__compact">
          <div class="device-info__icon">
            {{ getDeviceIcon(info.type) }}
          </div>
          <div class="device-info__basic">
            <span class="device-info__type">{{ getDeviceTypeText(info.type) }}</span>
            <span class="device-info__size">{{ info.screen?.width }}√ó{{ info.screen?.height }}</span>
          </div>
          <div v-if="showRefresh" class="device-info__actions">
            <button class="device-info__refresh-btn" title="Âà∑Êñ∞" @click="refresh">
              üîÑ
            </button>
          </div>
        </div>
      </template>

      <!-- ËØ¶ÁªÜÊ®°Âºè -->
      <template v-else>
        <div class="device-info__header">
          <div class="device-info__title">
            <span class="device-info__icon">{{ getDeviceIcon(info.type) }}</span>
            <h3>{{ getDeviceTypeText(info.type) }}</h3>
          </div>
          <button v-if="showRefresh" class="device-info__refresh-btn" @click="refresh">
            Âà∑Êñ∞
          </button>
        </div>

        <div class="device-info__sections">
          <!-- Âü∫Êú¨‰ø°ÊÅØ -->
          <div class="device-info__section">
            <h4>Âü∫Êú¨‰ø°ÊÅØ</h4>
            <div class="device-info__grid">
              <div class="device-info__item">
                <label>ËÆæÂ§áÁ±ªÂûã</label>
                <span>{{ getDeviceTypeText(info.type) }}</span>
              </div>
              <div class="device-info__item">
                <label>Â±èÂπïÊñπÂêë</label>
                <span>{{ getOrientationText(info.orientation) }}</span>
              </div>
              <div class="device-info__item">
                <label>Ëß¶Êë∏ÊîØÊåÅ</label>
                <span>{{ info.features?.touch ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}</span>
              </div>
            </div>
          </div>

          <!-- Â±èÂπï‰ø°ÊÅØ -->
          <div class="device-info__section">
            <h4>Â±èÂπï‰ø°ÊÅØ</h4>
            <div class="device-info__grid">
              <div class="device-info__item">
                <label>ËßÜÂè£Â∞∫ÂØ∏</label>
                <span>{{ info.screen?.width }}√ó{{ info.screen?.height }}</span>
              </div>
              <div class="device-info__item">
                <label>ËÆæÂ§áÂÉèÁ¥†ÊØî</label>
                <span>{{ info.screen?.pixelRatio }}</span>
              </div>
              <div class="device-info__item">
                <label>ÂèØÁî®Â∞∫ÂØ∏</label>
                <span>{{ info.screen?.availWidth }}√ó{{ info.screen?.availHeight }}</span>
              </div>
            </div>
          </div>

          <!-- ÊµèËßàÂô®‰ø°ÊÅØ -->
          <div class="device-info__section">
            <h4>ÊµèËßàÂô®‰ø°ÊÅØ</h4>
            <div class="device-info__grid">
              <div class="device-info__item">
                <label>ÊµèËßàÂô®</label>
                <span>{{ info.browser?.name }} {{ info.browser?.version }}</span>
              </div>
              <div class="device-info__item">
                <label>ÂºïÊìé</label>
                <span>{{ info.browser?.engine }}</span>
              </div>
            </div>
          </div>

          <!-- Êìç‰ΩúÁ≥ªÁªü‰ø°ÊÅØ -->
          <div class="device-info__section">
            <h4>Êìç‰ΩúÁ≥ªÁªü</h4>
            <div class="device-info__grid">
              <div class="device-info__item">
                <label>Á≥ªÁªü</label>
                <span>{{ info.os?.name }} {{ info.os?.version }}</span>
              </div>
              <div class="device-info__item">
                <label>Âπ≥Âè∞</label>
                <span>{{ info.os?.platform }}</span>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>

    <!-- Ëá™ÂÆö‰πâÊèíÊßΩ -->
    <div v-if="$slots.default" class="device-info__custom">
      <slot :device-info="info" :refresh="refresh" :is-loading="isLoading" />
    </div>
  </div>
</template>

<style scoped>
.device-info {
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  background: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.device-info--compact {
  padding: 12px;
}

.device-info--detailed {
  padding: 16px;
}

.device-info--loading {
  opacity: 0.7;
}

/* Âä†ËΩΩÁä∂ÊÄÅ */
.device-info__loading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 32px;
  color: #666;
}

.device-info__spinner {
  width: 20px;
  height: 20px;
  border: 2px solid #e1e5e9;
  border-top: 2px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ÈîôËØØÁä∂ÊÄÅ */
.device-info__error {
  display: flex;
  align-items: center;
  padding: 16px;
  color: #dc3545;
}

.device-info__error-icon {
  font-size: 24px;
  margin-right: 12px;
}

.device-info__error-content h4 {
  margin: 0 0 4px 0;
  font-size: 14px;
  font-weight: 600;
}

.device-info__error-content p {
  margin: 0 0 8px 0;
  font-size: 12px;
  color: #6c757d;
}

.device-info__retry-btn {
  padding: 4px 8px;
  font-size: 12px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Á¥ßÂáëÊ®°Âºè */
.device-info__compact {
  display: flex;
  align-items: center;
  gap: 12px;
}

.device-info__icon {
  font-size: 20px;
}

.device-info__basic {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.device-info__type {
  font-weight: 600;
  font-size: 14px;
}

.device-info__size {
  font-size: 12px;
  color: #6c757d;
}

/* ËØ¶ÁªÜÊ®°Âºè */
.device-info__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid #e1e5e9;
}

.device-info__title {
  display: flex;
  align-items: center;
  gap: 8px;
}

.device-info__title h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.device-info__refresh-btn {
  padding: 6px 12px;
  font-size: 12px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.device-info__refresh-btn:hover {
  background: #0056b3;
}

.device-info__sections {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.device-info__section h4 {
  margin: 0 0 8px 0;
  font-size: 14px;
  font-weight: 600;
  color: #495057;
}

.device-info__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 8px;
}

.device-info__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  background: #f8f9fa;
  border-radius: 4px;
}

.device-info__item label {
  font-size: 12px;
  color: #6c757d;
  font-weight: 500;
}

.device-info__item span {
  font-size: 12px;
  font-weight: 600;
}

/* Ëá™ÂÆö‰πâÂÜÖÂÆπ */
.device-info__custom {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #e1e5e9;
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
  .device-info__grid {
    grid-template-columns: 1fr;
  }

  .device-info__header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
}
</style>
